@font-face {
    font-family: "Gilroy-XB";
    src: url("../assets/Gilroy-ExtraBold.otf") format("truetype");
}

@font-face {
    font-family: "Gilroy-SB";
    src: url("../assets/Gilroy-SemiBold.otf") format("truetype");
}

@font-face {
    font-family: "Gilroy-R";
    src: url("../assets/Gilroy-Regular.otf") format("truetype");
}

.main_heading_area_container {
    position: relative;
    /* height: 100vh; */
    width: 100vw;
    display: flex;
    margin-top: 10%;
    /* margin-bottom: 10%; */
    justify-content: center;
    align-items: center;
    text-align: center;
}
.tagline{
    font-family: 'Gilroy-XB';
    font-size: 5vw;
    width: 80vw;
}
.features_head {
    font-family: "Gilroy-XB";
    justify-content: center;
    /* font-size: 2vw; */
    margin-top: 10%;
    display: flex;
}
.section-1-text{
    display: flex;
    justify-content: center;
    align-items: center;
}
.main_heading_text {
    color: rgb(248, 248, 248);
    font-family: 'Gilroy-XB';
