    justify-content: center;
    align-items: center; */
}

#header-btn {
    padding: 1vw 2vw;
    border-radius: 5vw;
    font-family: 'Gilroy-SB';
    background-color: white;
    color: black;
    border: white;
    /* margin-top: 15vh; */
    /* font-size: 2vw; */
}

#header-btn:focus {
    outline: none!important;
    box-shadow: none!important;
}

#experience-modal {
    /* background-color: whitesmoke; */
}

#contained-modal-title-vcenter {
    font-family: 'Gilroy-SB';
    /* font-size: 2vw; */
}

#experience-modal-body {
    font-family: 'Gilroy-R'!important;
    font-size: 1.5vw!important;
}

#experience-modal-footer {
    font-family: 'Gilroy-SB';
    justify-content: flex-start!important;
}

#experience-modal-button {
    padding: 0.5vw 1.5vw;
    border-radius: 5vw;
    font-family: 'Gilroy-SB';
    background-color: black;
    color: white;
    border: white;
    /* margin-top: 15vh; */
    /* font-size: 1.5vw; */
    /* width: 50%; */
}

.MuiRadio-colorSecondary.Mui-checked {
    color: #1e90ff!important;
}

#experience-modal-body .MuiFormLabel-root {
    font-family: 'Gilroy-R'!important;
}

#experience-modal-body .MuiTypography-body1 {
    font-family: 'Gilroy-R'!important;
}

.experience-modal-inputs {
    /* width: 100%!important;
    display: flex;
    flex-direction: row; */
    /* display: grid;
    grid-template-columns: 1fr 1fr ; */
}

#input-with-icon-grid {
    width: 80%!important;
}

.option-explanation {
    font-family: 'Gilroy-SB'!important;
    font-size: 1vw;
    color: #1e90ff;
}

#experience-modal-form {
    display: grid;
    grid-template-columns: 1fr 1fr 1fr;
    column-gap: 5%
}

@media (max-width: 750px) {
    #experience-modal-form {
        grid-template-columns: 1fr 1fr;
    }
}

